.card {
  padding-top: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
}

#us{
  max-width: 15%;
  max-height: 15%;
  margin: auto;
  display: block;
}

#about{
  text-align: center;
  margin: auto;
  display: block;
}

#aboutCard  {
  height: 20rem;
}

.hr {
border-top: 1px solid red;
}

h1.page-title {
color: midnightblue;
font-family: 'Roboto', "Ariel", monospace;
font-style: italic;
font-weight: 400;
src: url(https://fonts.gstatic.com/s/roboto/v20/KFOkCnqEu92Fr1Mu51xMIzIFKw.woff2) format('woff2');
unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
font-size: 7vw;
}

Navbar {
  font-size: 203px;
}

#blog {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
}
#desc {
  text-align: left;
}


.responsive {
  width: 100%;
  height: auto;
}

.social-svg {
  padding-top: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
}

#social  {
  height: auto;
  margin: 0 auto;
  padding-left: 33%;
  position: relative;

}

#icon-continer{
  display: inline;
  width: 92%;
}

.blinking{
    animation:blinkingText 1.2s infinite;
}
@keyframes blinkingText{
    0%{     color: #000;    }
    49%{    color: #000; }
    60%{    color: transparent; }
    99%{    color:transparent;  }
    100%{   color: #000;    }
}
